import { ReactNode } from 'react';
import Button from '@material-ui/core/Button';
import { FormattedMessage } from 'react-intl';

import Dialog from '../Dialog';
import './style.scss';

interface ConfirmDialogProps {
  onClose: (confirmed: boolean) => void;
  open: boolean;
  title?: ReactNode;
  message?: ReactNode;
}

const ConfirmDialog = ({ onClose, open, title, message }: ConfirmDialogProps) => {
  return (
    <div>
      <Dialog title={title || ''} onClose={() => onClose(false)} open={open}>
        <div className="message-wrapper">{message}</div>
        <div className="button-wrapper">
          <Button color="primary" onClick={() => onClose(true)}>
            Okay
          </Button>
          <Button color="primary" onClick={() => onClose(false)}>
            <FormattedMessage id="cancel" />
          </Button>
        </div>
      </Dialog>
    </div>
  );
};

export default ConfirmDialog;
